}

[data-theme="fancy"] .card-modern {
  background: linear-gradient(45deg, var(--card-bg), var(--glass-bg));
  border: 2px solid var(--primary-color);
  box-shadow: var(--glow-primary);
  border-radius: 20px;
}

[data-theme="fancy"] .btn-primary {
  background: linear-gradient(45deg, #bb86fc, #9c27b0);
