  border-radius: 20px;
}

[data-theme="fancy"] .card-modern:hover {
  box-shadow: 0 0 30px rgba(187, 134, 252, 0.8);
}

/* Responsive adjustments for all themes */
@media (max-width: 768px) {
  .svg-icon-animated:hover {
    transform: scale(1.05);
  }
  
  .theme-toggle {
    padding: 8px 16px;
    font-size: 12px;
  }
}

