.theme-fancy { background: linear-gradient(135deg, #ff9a9e, #fecfef); }
.theme-cyber { background: linear-gradient(135deg, #0f3460, #e94560); }
.theme-sunset { background: linear-gradient(135deg, #fa709a, #fee140); }
.theme-ocean { background: linear-gradient(135deg, #2196f3, #21cbf3); }
.theme-forest { background: linear-gradient(135deg, #11998e, #38ef7d); }
