/* Dashboard Grid */
.dashboard-grid-pro {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2rem;
}

.quick-actions-section-pro, .activities-section-pro, .chart-section-pro {
  background: white;
  border-radius: 16px;
  padding: 1.5rem;
  border: 1px solid #e2e8f0;
  animation: slideInUp 0.8s ease-out;
}

.section-subtitle {
  font-size: 1.125rem;
  font-weight: 600;
  color: #1e293b;
  margin-bottom: 1rem;
}

/* Quick Actions */
.quick-actions-grid-pro {
  display: grid;
  grid-template-columns: 1fr;
  gap: 0.75rem;
}

.quick-action-pro {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
