  background: #f8fafc;
  transform: translateX(4px);
}

.action-icon {
  font-size: 1.5rem;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(var(--action-color-rgb), 0.1);
  border-radius: 8px;
}

.action-content h4 {
  font-size: 0.875rem;
  font-weight: 600;
  color: #1e293b;
  margin: 0 0 0.25rem 0;
}

.action-content p {
  font-size: 0.75rem;
  color: #64748b;
  margin: 0;
}

.action-arrow {
  margin-left: auto;
  color: #94a3b8;
  font-weight: 600;
}

/* Activities */
.activities-list-pro {
  space-y: 1rem;
}

.activity-card {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  padding: 1rem;
  border-radius: 12px;
  transition: all 0.2s ease;
  animation: slideInRight 0.6s ease-out;
}

.activity-card:hover {
  background: #f8fafc;
}

.activity-card.priority-urgent {
  border-left: 4px solid #ef4444;
}

.activity-card.priority-high {
  border-left: 4px solid #f59e0b;
}

.activity-card.priority-medium {
  border-left: 4px solid #10b981;
}

.activity-icon {
  font-size: 1.25rem;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f1f5f9;
  border-radius: 8px;
  flex-shrink: 0;
}

.activity-content {
  flex: 1;
}

.activity-title {
  font-size: 0.875rem;
  font-weight: 600;
  color: #1e293b;
  margin: 0 0 0.25rem 0;
}

.activity-description {
  font-size: 0.75rem;
  color: #64748b;
  margin: 0 0 0.25rem 0;
}

.activity-time {
  font-size: 0.6875rem;
  color: #94a3b8;
}

.activity-priority {
  font-size: 1rem;
  flex-shrink: 0;
}

.view-all-btn {
  width: 100%;
  padding: 0.75rem;
  border: 1px dashed #cbd5e1;
  border-radius: 8px;
  background: transparent;
  color: #64748b;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-top: 1rem;
}

.view-all-btn:hover {
  border-color: #3b82f6;
  color: #3b82f6;
  background: rgba(59, 130, 246, 0.05);
}

/* Performance Chart */
.performance-chart-pro {
  height: 200px;
}

.chart-header-pro {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.chart-legend-pro {
  display: flex;
  gap: 1rem;
}

.legend-item-pro {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.75rem;
  color: #64748b;
}

.legend-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

.legend-dot.revenue {
  background: #3b82f6;
}

.legend-dot.orders {
  background: #10b981;
}

.chart-content-pro {
  display: flex;
  justify-content: space-between;
  align-items: end;
  height: 140px;
  padding: 0 0.5rem;
}

.chart-day-pro {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  animation: slideInUp 0.8s ease-out;
}

.day-bars {
  display: flex;
  gap: 2px;
  align-items: end;
  height: 100px;
  margin-bottom: 0.5rem;
}

.bar {
  width: 8px;
  border-radius: 4px 4px 0 0;
  animation: barGrowPro 1.5s ease-out;
}

.revenue-bar {
  background: linear-gradient(to top, #3b82f6, #60a5fa);
}

.orders-bar {
  background: linear-gradient(to top, #10b981, #34d399);
}

.day-label {
  font-size: 0.6875rem;
  color: #94a3b8;
  text-align: center;
}

/* Background Elements */
.professional-bg-elements {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.bg-grid {
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(148, 163, 184, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(148, 163, 184, 0.05) 1px, transparent 1px);
  background-size: 50px 50px;
}

.bg-gradient-1, .bg-gradient-2 {
  position: absolute;
  border-radius: 50%;
  filter: blur(100px);
  opacity: 0.3;
}

.bg-gradient-1 {
  width: 400px;
  height: 400px;
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  top: -200px;
  right: -200px;
}

.bg-gradient-2 {
  width: 300px;
  height: 300px;
  background: linear-gradient(135deg, #10b981, #059669);
  bottom: -150px;
  left: -150px;
}

/* Animations */
@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes barGrowPro {
  from {
    height: 0;
    opacity: 0;
  }
  to {
    height: var(--bar-height, 100%);
    opacity: 1;
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.8;
    transform: scale(1.05);
  }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .dashboard-grid-pro {
    grid-template-columns: 1fr;
  }
  
  .metrics-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
  
  .header-main {
    padding: 1.5rem 2rem 1rem;
  }
  
  .dashboard-content-pro {
    padding: 1.5rem 2rem;
  }
  
  .section-tabs {
    padding: 0 2rem;
  }
}

@media (max-width: 768px) {
  .header-main {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  
  .welcome-title-pro {
    font-size: 1.75rem;
  }
  
  .metrics-grid {
    grid-template-columns: 1fr;
  }
  
  .dashboard-content-pro {
    padding: 1rem;
  }
  
  .section-tabs {
    padding: 0 1rem;
    justify-content: flex-start;
  }
  
  .section-tab {
    padding: 0.75rem 1rem;
  }
}
.ultra-modern-dashboard {
  position: relative;
  min-height: 100vh;
  background: linear-gradient(135deg, 
    rgba(24, 24, 27, 0.95) 0%, 
    rgba(39, 39, 42, 0.95) 50%, 
    rgba(24, 24, 27, 0.95) 100%);
  overflow: hidden;
  padding: 2rem;
}

/* Loading Animation */
.ultra-modern-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.loading-container {
  text-align: center;
  color: white;
}

.loading-orb {
  position: relative;
  width: 100px;
  height: 100px;
  margin: 0 auto 2rem;
}

.orb-core {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40px;
  height: 40px;
  background: radial-gradient(circle, #fff 0%, #e0e7ff 100%);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  animation: pulse 2s ease-in-out infinite;
}

.orb-ring {
  position: absolute;
  top: 50%;
  left: 50%;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  animation: spin 3s linear infinite;
}

.ring-1 { width: 60px; height: 60px; animation-duration: 2s; }
.ring-2 { width: 80px; height: 80px; animation-duration: 3s; animation-direction: reverse; }
.ring-3 { width: 100px; height: 100px; animation-duration: 4s; }

.loading-text {
  font-size: 1.2rem;
  font-weight: 600;
  opacity: 0.9;
  animation: fadeInOut 2s ease-in-out infinite;
}

/* Header Section */
.dashboard-header {
  position: relative;
  background: linear-gradient(135deg, 
    rgba(79, 172, 254, 0.1) 0%, 
    rgba(240, 147, 251, 0.1) 100%);
  border-radius: 2rem;
  padding: 3rem 2rem;
  margin-bottom: 3rem;
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 2;
}

.welcome-section h1 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.user-name-highlight {
  position: relative;
  display: inline-block;
}

.user-name-highlight::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, #667eea 0%, #764ba2 100%);
  border-radius: 2px;
  animation: shimmer 2s ease-in-out infinite;
}

.welcome-subtitle {
